.footer {
    grid-column: full-start / full-end;
    background-color: $color-grey-light;
    padding: 2.4rem 0;

    display: grid;
    grid-template-columns: inherit;
    grid-gap: inherit;
    align-items: center;

}

.drupal {
    grid-column: content-start / content-end;

    &__link {
        font-style: none;
        text-decoration: none;
        color: $color-primary;

    }
}
